@import url(https://fonts.googleapis.com/css2?family=Girassol&family=Newsreader&family=Open+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Girassol&family=Open+Sans&display=swap);
body {
  margin: 0;
}
header {
  font-family: "Newsreader", serif;
  border-top-right-radius: 1rem;
  border-bottom-left-radius: 1rem;
  max-width: 600px;
  margin: auto;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  color: white;
  height: 3rem;
  background-color: black;
}
.inputDiv {
  font-family: "Open Sans", sans-serif;
  border-radius: 1rem;
  padding: 0.5rem;
  max-width: 300px;
  margin: 1rem auto;
  background-color: #fef2f2;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: column;
          flex-direction: column;
}
.inputDiv-amt {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}
.inputDiv-desc {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}
input {
    max-width: 100%;
  padding: 1rem;
  margin: 0.5rem;
  border: none;
  border-bottom: 2px solid white;
}
input:focus {
  border-radius: 5px;
  outline: none;
  border: 2px solid black;
}
button {
  font-size: 1rem;
  margin-top: 0.5rem;
  padding: 0.5rem 1rem;
}
button:enabled {
  border-radius: 3px;
  border: none;
  background-color: black;
  color: white;
  cursor: pointer;
}
button:disabled {
  cursor: not-allowed;
}
.output {
  margin-top: 10px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.expenseItem{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  padding: 1rem;
  border-radius: 3px;
  max-width: 300px;
  margin: auto;
  box-shadow: 0 0 3px black;
}
.expenseItem div{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
}
#delete{
    cursor: pointer;
    font-size: x-large;
    color: red;
    margin-left: 1rem;
}
